.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.sectionTitle {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 2.5rem;
  color: #111;
  text-align: center;
  letter-spacing: -0.02em;
}

/* Featured Section */
.featuredSection {
  margin-bottom: 6rem;
}

.featuredCard {
  background: #ffffff;
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
  /* Premium shadow */
  transition: transform 0.3s cubic-bezier(0.2, 0.8, 0.2, 1);
  display: grid;
  grid-template-columns: 1.6fr 1fr;
  gap: 0;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.featuredVideoWrapper {
  aspect-ratio: 16/9;
  width: 100%;
  height: 100%;
}

.featuredVideoWrapper lite-youtube {
  width: 100%;
  height: 100%;
  border-radius: 0;
  /* Flush with container */
}

.featuredContent {
  padding: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(145deg, #ffffff 0%, #f8faff 100%);
}

.featuredText {
  font-size: 1.35rem;
  line-height: 1.7;
  color: #333;
  font-style: italic;
  position: relative;
  font-weight: 500;
}

.featuredText::before {
  content: 'â€œ';
  font-size: 5rem;
  color: #3b82f6;
  /* Accent color - adjust if user has brand color */
  opacity: 0.2;
  position: absolute;
  top: -2.5rem;
  left: -2rem;
  font-family: serif;
  line-height: 1;
}

/* Grid Section */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 2.5rem;
}

.card {
  background: #fff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: column;
}

.card:hover {
  transform: translateY(-6px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
}

.videoWrapper {
  width: 100%;
  aspect-ratio: 16/9;
}

.cardText {
  padding: 1.75rem;
  font-size: 1.05rem;
  line-height: 1.6;
  color: #555;
  background: #fff;
  flex-grow: 1;
}

/* Responsive */
@media (max-width: 960px) {
  .featuredCard {
    grid-template-columns: 1fr;
  }

  .featuredContent {
    padding: 2.5rem;
    order: 2;
  }

  .featuredVideoWrapper {
    order: 1;
  }
}

@media (max-width: 600px) {
  .container {
    padding: 2rem 1rem;
  }

  .sectionTitle {
    font-size: 1.75rem;
    margin-bottom: 2rem;
  }

  .grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .featuredContent {
    padding: 2rem 1.5rem;
  }

  .featuredText {
    font-size: 1.15rem;
  }
}